<template>
    <div class="categories">
        <header>
            <h1> Categories page</h1>
            <!--
                binded radio inputs to 'APIendPoint' to allow for different axios requests depending on user selection.
             -->
            <div>
                <input type="radio" value="Food" v-model="APIendPoint">
                <label for="food">Food</label>

                <input type="radio" value="Exercise" v-model="APIendPoint">
                <label for="exercise">Exercise</label>
            </div>

        </header>

        <div class="container">

            <div v-show="APIendPoint === 'Food'">
                <h2>Search API for natural food</h2>

                <div class="foodSearch">
                    <form>
                        <select v-model="foodType">
                            <option v-for="(option,index) in options" :value="option.type" 
                            v-bind:key="(option,index)">
                                {{option.type}}
                            </option>
                        </select>

                        <!-- 'compQuery' will be used to complete the GET url -->
                        <input type="text" v-model.lazy="foodQuery">
                        <button @click="getFood()">Search food</button>   
                    </form>
                </div>

                <div class="foodContainer">    
                <!-- for all items from 'foodInfo' container-->
                    <div v-for="(item, index) in foodInfo" 
                    v-bind:key="(item,index)">
                        <div id="foodInfo">
                            <p>{{item.food_name.split(',')[0]}}</p>
                            <img :src="item.photo.thumb" alt="itemImg" width="auto" height="100px" id="photo">
                        </div>
                    </div>
                </div>
            </div>
            
            
            <div v-show="APIendPoint === 'Exercise'" class="exerciseSearch">
                <!-- this form is used to aquire user input that is then POSTed to the API-->
                <form>
                    <ul class="formContainer">
                        
                        <li class="form-row">
                            <label for="exerciseQuery" class="form-row"> Exercise 
                                <input type="text" v-model="exercise" placeholder="ran 2 miles" class="form-row">
                            </label>
                        </li>

                        <li class="form-row">
                            <label for="gender" class="form-row"> Gender 
                                <select v-model="gender" class="form-row"> 
                                    <option value="male">Male</option>
                                    <option value="female">Female</option>
                                </select>
                            </label>
                        </li>

                        <li class="form-row">
                            <label for="weight" class="form-row"> Weight (Kg) 
                                <input type="int" v-model="weight" placeholder="82" class="form-row">
                            </label>
                        </li>

                        <li class="form-row">
                            <label for="height" class="form-row"> Height (cm) 
                                <input type="int" v-model="height" placeholder="182" class="form-row">
                            </label>
                        </li>

                        <li class="form-row">
                            <label for="age" class="form-row"> Age           
                                <input type="int" v-model="age" placeholder="22" class="form-row">
                            </label>
                        </li>

                    
                    <button @click="postExercise()">get exercise</button>
                    </ul>
                </form>


                <div class="exerciseInfo" v-for="(ex, index) in exerciseInfo" 
                    v-bind:key="(ex, index)" >
                    <p>Exercise Name: {{ex[0].name}}</p>
                    <p>Duration (min): {{Math.round(ex[0].duration_min)}}</p>
                    <p>Calories Burned: {{Math.round(ex[0].nf_calories)}}</p>
                    <p>MET <a href="https://en.wikipedia.org/wiki/Metabolic_equivalent_of_task">(Metabolic equivalent of task)</a>: {{ex[0].met}}</p>
                </div>

            
            </div>


            

        </div>
           
        

         <!-- this div displays response data stored in 'exerciseInfo' from the POST request -->



    <Navigation/>
    <Footer/>
    </div>
</template>

<script>
import Footer from '@/components/Footer.vue';
import Navigation from '@/components/Navigation.vue';


export default {
    name: 'categories',
    components: {
        Footer,
        Navigation
    },

    data() {
        return {
            config: {
                headers: {
                    'x-app-id': 'd1bcf11d',
                    'x-app-key': '84c1b7fa584873b8afe60312db88d3e8'
                }
            },
            foodQuery: null, 
            foodInfo: null, 
            APIendPoint: null,
                exercise: 'ran 2 miles',
                gender: 'male',
                weight: '92',
                height: '182',
                age: 22,
            exerciseInfo: null,
            options: [
                {type:'branded'},
                {type:'common'},
            ],
            foodType: null,
            
        }
    },
    

    methods: {
        //getFood method returns data to 'foodInfo' by completing the GET url using 'foodQuery' user input  
        getFood: function() {
            axios
                .get('https://trackapi.nutritionix.com//v2/search/instant?query='+this.foodQuery+"'",this.config)
                .then(Response => {
                    if (this.foodType === 'branded'){
                        this.foodInfo = Response.data.branded
                    } else {
                        this.foodInfo = Response.data.common
                    }
                })
        },

        //postExercise returns data to 'exerciseInfo', the POST request is completed by the user using a form 
        postExercise: function() {
            axios
                .post('https://trackapi.nutritionix.com/v2/natural/exercise', 
                    {
                        "query": this.exercise,
                        "gender": this.gender,
                        "weight_kg": this.weight,
                        "height_cm": this.height,
                        "age": this.age
                    }, this.config)

                .then(Response => {
                    this.exerciseInfo = Response.data
                })
        }
    }
}

</script>

